  src: local("Montserrat-ExtraBold"), local("Montserrat-ExtraBold"), url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 16px;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  background-color: #00bdca;
  min-width: 320px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
}

.block {
  display: flex;
}

.header {
  display: flex;
  margin: 60px 0 0 100px;
}

.header__logo {
  display: flex;
  align-items: center;
}

.header__logo img {
  width: 44px;
  height: 44px;
  margin-right: 12px;
}

.header__logo .title {
  line-height: 1;
  font-size: 13px;
  font-weight: 500;
}

.header__logo .title h1 {
  font-size: 24px;
  font-weight: 600;
}

.header__logo .title h1 span {
  color: #56ecf6;
}

.header__menu {
  margin-left: 144px;
}

.nav {
  display: flex;
  align-items: center;
  line-height: 3.3;
}

.navigation__item {
  margin-right: 34px;
}

.header__login {
  margin-left: 5px;
  margin-right: 8px;
}

.header__login .login_log {
  margin-right: 30px;
}

.header__login .login_sign {
  display: inline-block;
  min-width: 120px;
  line-height: 1;
  text-align: center;
  padding: 13px;
  background: #fff;
  color: #1d354e;
  border-radius: 16px;
}

.header__lang {
  position: relative;
  z-index: 2;
  top: 5px;
}

.no-touch .header__lang:hover .lang__list {
  opacity: 1;
  visibility: visible;
}

.no-touch .header__lang:hover .lang__current {
  border-radius: 16px 16px 0 0;
}

.header__lang .lang__current {
  min-width: 80px;
  text-align: center;
  display: inline-block;
  line-height: 1;
  padding: 13px;
  background: #fff;
  border-radius: 16px;
  color: #1d354e;
}

.no-touch .header__lang .lang__current:hover {
  text-decoration: underline;
}

.header__lang .lang__list {
  position: absolute;
  width: 100%;
  top: 70%;
  left: 0;
  opacity: 0;
  visibility: hidden;
  padding: 13px;
  background: #fff;
  border-radius: 0 0 16px 16px;
  text-align: center;
  display: inline-block;
}

.no-touch .header__lang .lang__list:hover {
  text-decoration: underline;
}

.header__lang .lang__list .lang_extra {
  color: #1d354e;
}

.button {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #1d354e;
  padding: 22px 67px;
  font-size: 18px;
  font-weight: 600;
  font-family: "Montserrat", "Helvetica", sans-serif;
  border-radius: 32px;
  text-align: center;
  outline: none;
  cursor: pointer;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
  outline: none;
}

.no-touch .button:hover {
  background-color: #244261;
}

.button:active {
  background-color: #16283b;
}

.robot_container {
  position: relative;
}

.robot__img {
  position: absolute;
  top: -135px;
  left: -78px;
  pointer-events: none;
  user-select: none;
}

.robot__heading {
  max-width: 600px;
  text-align: center;
  margin: 118px 86px 0 auto;
  line-height: 1.2;
}

.robot__heading h2 {
  font-size: 72px;
  font-weight: 600;
}

.robot__heading p {
  font-size: 30px;
  font-weight: 600;
  margin: 7px 0 20px 13px;
  line-height: 1.6;
}

.robot__tablet-wrapper {
  width: 100%;
  background: #fff;
  height: 720px;
  border-radius: 16px;
  margin-top: 210px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: -1;
}

.robot__tablet-inner {
  min-width: 1074px;
  min-height: 624px;
  border-radius: 8px;
  background: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
}

.robot__tablet-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.robot__bg {
  width: 400%;
  height: 355%;
  transform: rotate(30deg);
  background: #1d354e;
  position: absolute;
  left: -140%;
  top: 1175px;
  z-index: -3;
}

.robot__box1 {
  position: absolute;
  top: 396px;
  left: 7%;
  z-index: -1;
}

.robot__box2 {
  position: absolute;
  top: 545px;
  right: 10%;
}

.forecast {
  margin: 140px auto 0;
  position: relative;
}

.forecast__container {
  max-width: 1000px;
}

.forecast__heading {
  font-size: 60px;
  font-weight: 600;
  margin: 41px 0 2px 0;
  padding: 0 18px;
}

.forecast__desc {
  font-size: 24px;
  font-weight: 600;
  padding: 0 18px;
}

.forecast__currency {
  display: flex;
  margin-top: 40px;
  margin-left: 15px;
}

.forecast__currency li {
  margin-right: 30px;
  width: 136px;
  height: 84px;
}

.forecast__box1 {
  position: absolute;
  top: -65px;
  right: 7.5%;
}

.forecast__box2 {
  position: absolute;
  top: 60px;
  left: 0;
}

.currency__item {
  display: block;
  text-align: center;
  text-transform: uppercase;
  border: 8px solid #223e5c;
  background: transparent;
  width: 136px;
  height: 84px;
  line-height: 68px;
  border-radius: 8px;
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  transition: .3s;
}

.no-touch .currency__item:hover {
  background: #00bdca;
  border: 8px solid #00bdca;
}

.graph {
  margin: 40px auto 0;
  position: relative;
}

.graph__img {
  display: block;
  margin: 0 auto;
}

.indicators {
  display: flex;
  position: absolute;
  left: 27%;
  top: 5.5%;
  line-height: 1.3;
}

.indicators__item {
  margin-right: 45px;
}

.indicator__main {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
}

.indicator__main_centered {
  line-height: 2.6;
}

.indicator__aside {
  font-size: 12px;
  font-weight: 500;
  text-align: right;
}

.indicator__aside span {
  color: #71d44e;
}

article {
  margin-top: 142px;
}

.article {
  height: 1160px;
  background: #fff;
  color: #1d354e;
  border-radius: 16px;
}

.article h3 {
  font-size: 60px;
  font-weight: 600;
  padding: 73px 0 30px 96px;
}

.post {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 82%;
  width: 83%;
  margin: 0 auto;
}

.post__item {
  display: flex;
  width: 450px;
  margin-bottom: 35px;
}

.post__item:nth-child(4) {
  margin-left: 75px;
}

.post__item:nth-child(5) {
  margin-left: 75px;
}

.post__item:nth-child(6) {
  margin-left: 75px;
}

.post__item svg {
  width: 20%;
}

.post__item p {
  font-size: 18px;
  font-weight: 300;
  width: 95%;
  position: relative;
  top: -10px;
  line-height: 2;
}

.news-wrapper {
  margin: 108px auto 48px;
  width: 83%;
}

.news-wrapper h3 {
  font-size: 60px;
  font-weight: 600;
}

.news {
  display: flex;
  margin-top: 26px;
}

.news__item {
  margin-right: 36px;
}

.news__item h4 {
  font-size: 30px;
  font-weight: 600;
  margin: 15px 0;
  line-height: 1.25;
}

.news__item p {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
}

.news__img {
  width: 300px;
  height: 300px;
  border-radius: 8px;
  background: #222;
}

.news__button {
  margin: 30px 0;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  padding: 22px 30px;
  min-width: 320px;
}

.news__box {
  position: absolute;
  right: 8%;
  top: 15%;
}
