.news-wrapper {
	margin: 108px auto 48px;
	width: 83%;
	& h3 {
		@include f(60px, 600); } }
.news {
	display: flex;
	margin-top: 26px; }
.news__item {
	margin-right: 36px;
	& h4 {
		@include f(30px, 600);
		margin: 15px 0;
		line-height: 1.25; }
	& p {
		@include f(14px, 400);
		line-height: 1.6; } }
.news__img {
	width: 300px;
	height: 300px;
	border-radius: 8px;
	background: #222; }

.news__button {
	margin: 30px 0;
	position: relative;
	left: 50%;
	transform: translateX(-50%);
	padding: 22px 30px;
	min-width: 320px; }

.news__box {
	position: absolute;
	right: 8%;
	top: 15%; }
