.robot {
	&_container {
		position: relative; }
	&__img {
		position: absolute;
		top: -135px;
		left: -78px;
		pointer-events: none;
		@include nosel; }
	&__heading {
		max-width: 600px;
		text-align: center;
		// margin-left: auto
		margin: 118px 86px 0 auto;
		line-height: 1.2;
		& h2 {
			@include f(72px, 600);
 }			// line-height: 1.2
		& p {
			@include f(30px, 600);
			margin: 7px 0 20px 13px;
			line-height: 1.6; } }

	&__tablet-wrapper {
		width: 100%;
		background: #fff;
		height: 720px;
		border-radius: 16px;
		margin-top: 210px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: -1; }
	&__tablet-inner {
		min-width: 1074px;
		min-height: 624px;
		border-radius: 8px;
		background: $darkgray;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__tablet-icon {
		@include vcenter;
		z-index: 1; }
	&__bg {
		width: 400%;
		height: 355%;
		@include rotate(30);
		background: $darkblue;
		position: absolute;
		left: -140%;
		top: 1175px;
		z-index: -3; }
	&__box1 {
		position: absolute;
		top: 396px;
		left: 7%;
		z-index: -1; }
	&__box2 {
		position: absolute;
		top: 545px;
		right: 10%; } }
